import React from "react";
import { NavLink } from "react-router-dom";
import styled from "styled-components";

import PageContent from "components/PageContent";

const AppMenuWrapper = styled.div`
  background-color: #444444;
  display: flex;

  @media ${({ theme }) => theme.devices.small.query} {
    height: 56px;
  }
`;

const AppMenuContent = styled(PageContent)`
  display: flex;
  flex-direction: column;
`;

const MobileMenuButton = styled.button`
  height: 56px;
  padding: 8px 16px;
  margin: 0 -16px;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
  font-family: "Roboto", sans-serif;
  font-size: large;
  color: #fff;
  text-align: left;

  @media ${({ theme }) => theme.devices.small.query} {
    display: none;
  }

  &:before {
    content: "";
    display: inline-block;
    width: 0;
    height: 0;
    border-left: 8px solid #fff;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;

    margin-right: 8px;
    transform-origin: 50% 50%;
    transition: transform 0.15s linear;
  }

  &.expanded:before {
    transform: rotate(90deg);
  }
`;

const NavList = ({ className, ...props }) => (
  <nav className={className}>
    <ul className="nav-list" {...props} />
  </nav>
);

const MenuNavList = styled(NavList)`
  flex: 1;
  display: flex;

  &.hidden {
    display: none;
  }

  @media ${({ theme }) => theme.devices.small.query} {
    &,
    &.hidden {
      display: flex;
    }
  }

  .nav-list {
    flex: 1;
    list-style-type: none;
    margin: 0;
    padding: 0 0 8px 0;
    display: flex;
    flex-direction: column;

    @media ${({ theme }) => theme.devices.small.query} {
      flex-direction: row;
      padding-bottom: 0;
    }
  }
`;

const NavListItem = ({ className, ...props }) => (
  <li className={className}>
    <NavLink className="nav-link" {...props} />
  </li>
);

const MenuNavListItem = styled(NavListItem)`
  display: flex;

  & ~ & {
    border-top: 1px solid #fff;
  }

  @media ${({ theme }) => theme.devices.small.query} {
    & ~ & {
      border-top: none;
    }
  }

  .nav-link {
    flex: 1;
    padding: 8px 16px;
    display: flex;
    align-items: center;

    &,
    &:visited {
      text-decoration: none;
      color: #fff;
    }

    &.active {
      color: red;
    }

    @media ${({ theme }) => theme.devices.small.query} {
      border-bottom: 2px solid transparent;

      &.active {
        border-bottom-color: red;
        color: #fff;
      }
    }
  }
`;

export default class AppMenu extends React.Component {
  state = {
    showMobileMenu: false,
  };

  handleToggleMobileMenu = () => {
    this.setState(state => ({ showMobileMenu: !state.showMobileMenu }));
  };

  render() {
    const { showMobileMenu } = this.state;

    const navItems = [
      { children: "Home", to: "/", exact: true },
      { children: "Contact", to: "/contact", exact: true },
    ];

    return (
      <AppMenuWrapper>
        <AppMenuContent>
          <MobileMenuButton
            onClick={this.handleToggleMobileMenu}
            className={showMobileMenu && "expanded"}
          >
            Menu
          </MobileMenuButton>

          <MenuNavList className={!showMobileMenu && "hidden"}>
            {navItems.map((navItem, idx) => (
              <MenuNavListItem key={idx} {...navItem} />
            ))}
          </MenuNavList>
        </AppMenuContent>
      </AppMenuWrapper>
    );
  }
}
