import React from "react";
import { withRouter } from "react-router";
import styled from "styled-components";
import { Helmet } from "react-helmet";

import AppBanner from "./AppBanner";
import AppMenu from "./AppMenu";
// import AppBar from "./AppBar";
import AppFooter from "./AppFooter";
import Router from "./Router";
import PageTitle from "./PageTitle";
import PageDescription from "./PageDescription";

const AppWrapper = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
`;

const AppContent = styled.main`
  flex: 1;
`;

function App({ location }) {
  const origin =
    process.env.REACT_APP_LOCATION_ORIGIN || window.location.origin;
  const baseUrl = origin + process.env.PUBLIC_URL;
  const url = baseUrl + location.pathname + location.search;

  return (
    <AppWrapper>
      <PageTitle title="Loïse Holive praticienne en médecine traditionnelle chinoise à Nantes" />
      <PageDescription description="Loïse Holive praticienne en médecine traditionnelle chinoise à Nantes" />

      <Helmet>
        <html lang="fr_FR" />
        <meta property="og:locale" content="fr_FR" />

        <link rel="canonical" href={url} />
        <meta property="og:url" content={url} />
        <meta
          property="og:site_name"
          content="Loïse Holive praticienne en médecine traditionnelle chinoise à Nantes"
        />

        {/* <meta property="fb:app_id" content="193733691137750" /> */}
        <meta property="og:type" content="website" />
        <meta property="og:image" content={baseUrl + "/og-image.png"} />
      </Helmet>

      <AppBanner />
      <AppMenu />

      {/* <AppBar /> */}

      <AppContent>
        <Router />
      </AppContent>

      <AppFooter />
    </AppWrapper>
  );
}

export default withRouter(App);
