import styled, { css } from "styled-components";

const breakpointsCss = ({ theme }) =>
  Object.entries(theme.devices).map(
    ([deviceName, { size, query }]) => css`
      @media ${query} {
        max-width: calc(
          ${size}px -
            calc(var(--grid-gutter) * ${deviceName === "small" ? 2 : 4})
        );
      }
    `
  );

const isPadded = ({ padded }) => (padded ? 1 : 0);

export default styled.div`
  width: 100%;
  margin: 0 auto;
  max-width: calc(100% - calc(var(--grid-gutter) * 2));

  --padding: calc(var(--grid-gutter) * ${isPadded});
  @media ${({ theme }) => theme.devices.medium.query} {
    --padding: calc(var(--grid-gutter) * 2 * ${isPadded});
  }

  padding: var(--padding) 0;

  ${breakpointsCss};
`;
